import React from 'react';
import Component from 'react-pure-render/component';
import KindbeetleIcon from 'icons/logo-kindbeetle.svg';
import Icon from 'components/Icon';
// import linkify from 'components/linkify';
import links from 'data/links.yml';
import text from 'data/text.yml';

export default class Footer extends Component {

  static contextTypes = {
    location: React.PropTypes.object,
  }

  render() {
    return (
      <div className={styles.root}>
        <div className={styles.content}>
          <KindbeetleIcon width={213} height={24} className={styles.logo} />
          <div className={styles.contacts}>
            {text.getInTouch[__LANG__]}<br />
            <a href={`mailto:${links.mailto}`}>{links.mailto}</a>
          </div>

          <a href={links.facebook} target='_blank' className={styles.facebook}>
            <Icon name='facebook' />
          </a>
        </div>
      </div>
    );
  }

}



