import React from 'react';
import ReactSwipe from 'react-swipe';
import Component from 'react-pure-render/component';
import TeamMember from 'components/TeamMember';
import TeamThumbs from 'components/TeamThumbs';
// import linkify from 'components/linkify';
import text from 'data/text.yml';

text.team[__LANG__].forEach(member => {
  if (!/^(https?:)/.test(member.pic)) {
    try {
      member.thumb = require(`data/faces/${member.thumb}`);
      member.pic = require(`data/faces/${member.pic}`);
    } catch (err) { /* lol ok */ }
  }
});

export default class Team extends Component {

  constructor(props) {
    super(props);
    this.state = {
      currentMemberIndex: 0,
      swiping: 0,
      dragging: false,
    };
  }

  componentDidMount() {
    window.addEventListener('touchend', () => {
      this.setState({ swiping: 0 });
    });
  }

  handleNavigate = (i) => this.setState({ currentMemberIndex: i })

  render() {
    const { currentMemberIndex, swiping } = this.state;
    const team = text.team[__LANG__];

    return (
      <div className={styles.root}>
        <h2>{text.teamTitle[__LANG__]}</h2>
        <div className={styles.mobile} key='mobile'>
          <ReactSwipe
            slideToIndex={currentMemberIndex}
            callback={(i) => {
              if (!isNaN(i)) {
                this.setState({ currentMemberIndex: i, swiping: 0 });
              }
            }}
            swiping={(percentage) => this.setState({ swiping: percentage })}
            transitionEnd={() => this.setState({ swiping: 0 })}
          >
            {team.map((member, i) => (
              <div
                key={i}
                className={currentMemberIndex === i ? styles.currentMember : styles.member}
              >
                <TeamMember member={member} />
              </div>
            ))}
          </ReactSwipe>
        </div>
        <div className={styles.desktop} key='desktop'>
          {team.map((member, i) => (
            <div
              key={i}
              className={currentMemberIndex === i ? styles.currentMember : styles.member}
            >
              <TeamMember member={member} />
            </div>
          ))}
        </div>

        <div className={styles.thumbs}>
          <TeamThumbs
            team={team}
            onNavigate={this.handleNavigate}
            currentIndex={currentMemberIndex + swiping}
            />
        </div>
      </div>
    );
  }

}



