import React from 'react';
import Component from 'react-pure-render/component';
import validator from 'validator';
import Spinner from 'components/Spinner';
import Button from 'components/Button';
import Footer from 'components/Footer';
import linkify from 'components/linkify';
import text from 'data/text.yml';
import emailJsConfig from 'data/emailjs.yml';
const emailjs = require('exports?emailjs!emailjs');

const MODE_PROGRESS = 'progress';
const MODE_SENT = 'sent';
const MODE_ERROR = 'error';
const MODE_INVALID = 'invalid';

function sendMail(data) {
  const dataMail = {
    email: data.email,
    name: data.name,
    message: data.text
  };
  emailjs.init(emailJsConfig.key);
  return emailjs.send('mailgun', 'kindbeetle', dataMail);
}

export default class Feedback extends Component {

  constructor() {
    super();
    this.state = {
      mode: null
    };
  }

  submitFeedback = async(e) => {
    e.preventDefault();

    const name = validator.trim(this.refs.name.value);
    const email = validator.trim(this.refs.email.value);
    const message = validator.trim(this.refs.text.value);

    if (!validator.isEmail(email) || !name.length || !message.length) {
      const errorField = !name.length ? 'name' : !validator.isEmail(email) ? 'email' : 'text'; // eslint-disable-line no-nested-ternary
      this.setState({
        mode: MODE_INVALID,
        field: errorField,
      });
      if (this.refs[errorField]) {
        this.refs[errorField].focus();
      }
      return;
    }

    this.setState({
      mode: MODE_PROGRESS
    });

    try {
      await sendMail({ name, email, text: message });

      this.setState({
        mode: MODE_SENT
      });

      this.refs.name.value = '';
      this.refs.email.value = '';
      this.refs.text.value = '';
    } catch (err) {
      this.setState({
        mode: MODE_ERROR
      });
    }
  };

  getSubmitResult(mode) {
    if (mode === MODE_SENT) {
      return (
        <div className={styles.submitSuccessResult}>
          {linkify(text.mail.sent[__LANG__])}
        </div>
      );
    } else if (mode === MODE_ERROR) {
      return (
        <div className={styles.submitResult}>
          {linkify(text.mail.error[__LANG__])}
        </div>
      );
    } else if (mode === MODE_INVALID) {
      return (
        <div className={styles.submitResult}>
          {linkify(text.mail.invalid[__LANG__])}
        </div>
      );
    }

    return null;
  }

  render() {
    const { mode, field } = this.state;
    console.log(mode);

    return (
      <div className={styles.root}>
        <div className={styles.form}>
          <div className={styles.content}>
            <h4>{linkify(text.mail.title[__LANG__])}</h4>
            <form onSubmit={this.submitFeedback}>
              <div className={styles.inputs}>
                <input
                  type='text'
                  placeholder={text.form.name[__LANG__]}
                  className={field === 'name' && styles.errorInput}
                  ref='name'
                  />
                <input
                  type='email'
                  placeholder={text.form.email[__LANG__]}
                  className={field === 'email' && styles.errorInput}
                  ref='email'
                  />
                <textarea
                  placeholder={text.form.text[__LANG__]}
                  className={field === 'text' && styles.errorInput}
                  ref='text'
                  />
              </div>

              {mode === MODE_PROGRESS &&
                <div className={styles.spinnerWrapper}><Spinner /></div>
              }
              {mode !== MODE_PROGRESS &&
                <Button
                  type='submit'
                  className={styles.submit}
                  onClick={this.submitFeedback}
                  theme='blackOutline'
                >
                  {text.form.button[__LANG__]}
                </Button>
              }

              {this.getSubmitResult(mode)}
            </form>
          </div>
        </div>

        <div className={styles.footerWrapper}>
          <Footer />
        </div>
      </div>
    );
  }

}



