import React from 'react';
import Component from 'react-pure-render/component';

const icons = {
  doc: '',
  facebook: '',
};

export default class Icon extends Component {
  render() {
    const {
      name,
      className,
      ...props,
    } = this.props;
    return (
      <span className={[styles.root, className].join(' ')} {...props}>
        {icons[name]}
      </span>
    );
  }
}



