import React from 'react';
import Component from 'react-pure-render/component';

import text from 'data/text.yml';
import links from 'data/links.yml';
import linkify from 'components/linkify';

const LANGS = ['ru', 'en'];

export default class Langs extends Component {

  static contextTypes = {
    location: React.PropTypes.object
  }

  render() {
    const path = this.context.location.pathname.replace(`${__PREFIX__}/`, '').replace(/\/$/, '');
    return (
      <div className={styles.root}>
        {LANGS.map(lang => (
          <a
            href={`${links.langLink[lang]}${path}`}
            key={lang}
            className={styles.link}
          >
            <div className={__LANG__ !== lang ? styles.iconLangActive : styles.iconLang}>
              {linkify(text.langICON[lang])}
            </div>
          </a>
        ))}
      </div>
    );
  }

}



