import React from 'react';
import Component from 'react-pure-render/component';
import Button from 'components/Button';
// import linkify from 'components/linkify';
import text from 'data/text.yml';

const WEB_PRES = {
  ru: require('data/files/web_ru.pdf'),
  en: require('data/files/web_en.pdf'),
};

const GRAPH_PRES = {
  ru: require('data/files/graph_ru.pdf'),
  en: require('data/files/graph_en.pdf'),
};

export default class Departments extends Component {

  render() {
    return (
      <div className={styles.root}>
        <div className={styles.devSection}>
          <div className={styles.sectionBg} />
          <div className={styles.sectionContent}>
            <h2 className={styles.headline}>
              {text.about.devTitle[__LANG__]}
            </h2>
            <p
              className={styles.lead}
              dangerouslySetInnerHTML={{
                __html: text.about.devLead[__LANG__]
              }}
              />
            <div className={styles.download}>
              <Button
                href={WEB_PRES[__LANG__]}
                component='a'
                icon='doc'
                theme='yellow'
                target='_blank'
              >
                {text.about.devPortfolio[__LANG__]}
              </Button>
            </div>
          </div>
        </div>
        <div className={styles.designSection}>
          <div className={styles.sectionBg} />
          <div className={styles.sectionContent}>
            <h2 className={styles.headline}>
              {text.about.designTitle[__LANG__]}
            </h2>
            <p
              className={styles.lead}
              dangerouslySetInnerHTML={{
                __html: text.about.designLead[__LANG__]
              }}
              />
            <div className={styles.download}>
              <Button
                href='#'
                component='a'
                icon='doc'
                theme='yellow'
                href={GRAPH_PRES[__LANG__]}
                target='_blank'
              >
                {text.about.graphPortfolio[__LANG__]}
              </Button>
            </div>
          </div>
        </div>
      </div>
    );
  }

}



