import React from 'react';
import { Link } from 'react-router';
import Component from 'react-pure-render/component';
import Button from 'components/Button';
import withShowed from 'components/withShowed';
import text from 'data/text.yml';
import links from 'data/links.yml';

const SLIDES = [
  require('img/home/slides/960x1080_1.jpg'),
  require('img/home/slides/960x1080_2.jpg'),
  require('img/home/slides/960x1080_2a.jpg'),
  require('img/home/slides/960x1080_3a.jpg'),
  require('img/home/slides/960x1080_3b.jpg'),
];

class Points extends Component {

  constructor(props) {
    super(props);
    this.state = {
      slideIndex: 0,
    };
  }

  componentDidMount() {
    this.interval = setInterval(() => {
      this.setState(({ slideIndex }) => ({
        slideIndex: (slideIndex >= SLIDES.length - 1) ? 0 : slideIndex + 1,
      }));
    }, 5000);
  }

  render() {
    const points = text.about.points[__LANG__];
    const { slideIndex } = this.state;
    return (
      <div className={styles.root}>
        <div className={styles.slideSection}>
          {SLIDES.map((slide, i) => (
            <div
              className={slideIndex === i ? styles.currentSlide : styles.slide}
              style={{
                backgroundImage: `url(${slide})`,
              }}
              key={i}
              />
          ))}
        </div>
        <div className={styles.listSection}>
          <h3>{points.title}</h3>
          <ul>
            {points.list.map((point, i) =>
              <li key={i}>{point}</li>
            )}
          </ul>

          <Button
            component={Link}
            theme='yellowGray'
            to={links.feedbackLink[__LANG__]}
          >
            {text.getInTouchButton[__LANG__]}
          </Button>
        </div>
      </div>
    );
  }

}

export default withShowed(Points);



