import React from 'react';
import { findDOMNode } from 'react-dom';
import Component from 'react-pure-render/component';
import { browserHistory } from 'react-router';
import throttle from 'lodash/function/throttle';

import InnerPage from 'routes/InnerPage';
import Departments from 'components/Departments';
import Points from 'components/Points';
import Clients from 'components/Clients';
import Team from 'components/Team';
import Feedback from 'components/Feedback';
import links from 'data/links.yml';

const sectionLinks = {
  home: links.home[__LANG__],
  clients: links.clientsLink[__LANG__],
  team: links.teamLink[__LANG__],
  feedback: links.feedbackLink[__LANG__],
};

export default class Home extends Component {

  constructor(props) {
    super(props);

    this.sections = {};
  }

  addSection = sectionName => ref => {
    if (ref) {
      this.sections[sectionName] = findDOMNode(ref);
    }
  }

  handleScroll = throttle(() => {
    const scrollSection = Object.keys(this.sections).reduce((current, section) => {
      const rect = this.sections[section].getBoundingClientRect();
      if ((rect.top - window.innerHeight * 0.5) <= 0 && rect.bottom > 0) {
        return section;
      }

      return current;
    }, 'home');
    const { section: currentSection } = this.props.params;

    if (currentSection !== scrollSection) {
      browserHistory.replace(sectionLinks[scrollSection]);
    }
  }, 100)

  goToSection(section = 'home') {
    const rect = this.sections[section].getBoundingClientRect();
    if (!((rect.top - window.innerHeight * 0.5) <= 0 && rect.bottom > 0)) {
      window.scrollTo(rect.left + window.scrollX, rect.top + window.scrollY);
    }
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.params.section !== nextProps.params.section) {
      this.goToSection(nextProps.params.section);
    }
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
    window.addEventListener('resize', this.handleScroll);
    this.goToSection(this.props.params.section);
  }

  render() {
    return (
      <InnerPage>
        <div ref={this.addSection('home')}>
          <Departments />
          <Points />
        </div>
        <Clients ref={this.addSection('clients')} />
        <Team ref={this.addSection('team')} />
        <Feedback ref={this.addSection('feedback')} />
      </InnerPage>
    );
  }

}



